.table-container {
  padding: 20px;
  background-color: #f0efef;
}
.table-container .inner-table {
  margin-top: 20px;
}
.table-container .inner-table tr {
  cursor: pointer;
}
.table-container .btn-container {
  margin: 0 0 20px 0;
}
.table-container .btn-container button {
  margin-left: 10px;
}
.table-container .btn-container button:first-child {
  margin-left: 0;
}
