.report-container {
  padding: 20px;
  background-color: #f0efef;
}
.report-container .top .ant-card {
  min-height: 270px;
}
.report-container .top .statusIcon {
  display: none;
}
.report-container .top .ant-card-head-title {
  font-size: 13px;
}
.report-container .top .ant-card-head {
  border-bottom: 0;
  margin-bottom: -10px;
}
.report-container .top .ant-card-body {
  padding-top: 0;
}
.report-container .top .ant-card-body .total {
  font-size: 26px;
  padding-bottom: 10px;
}
.report-container .top .ant-card-body .monthSale {
  padding: 10px 0 20px 0;
}
.report-container .top .ant-card-body .monthCompare {
  border: 0;
  padding: 0 0 10px 0;
}
.report-container .top .ant-card-body .average {
  padding: 15px 0;
  border-top: 1px solid lightgrey;
}
.report-container .top .ant-card-body .profit-month {
  display: flex;
  align-items: center;
}
.report-container .top .ant-card-body .profit-month canvas {
  max-height: 150px;
  max-width: 180px;
}
.report-container .top .ant-card-body .profit-month .g2-html-annotation {
  font-size: 14px !important;
  text-shadow: none !important;
}
.report-container .top .ant-card-body .g2-tooltip {
  padding: 15px !important;
}
.report-container .middle {
  margin-top: 30px;
}
.report-container .middle .ant-card-head {
  border-bottom: 0;
  margin-bottom: 30px;
}
.report-container .middle .g2-tooltip {
  padding: 15px !important;
}
