#login-container {
  text-align: center;
  position: fixed;
  width: 100%;
  background: url("../../img/background.jpg");
  z-index: 1;
  height: 100vh;
}
#login-container h2 {
  margin-top: 100px;
  letter-spacing: 3px;
  font-size: 38px;
  color: gray;
  text-shadow: 1px 1px #000;
}
#login-container #form-container {
  position: absolute;
  border: 2px grey solid;
  border-radius: 30px;
  margin-top: 80px;
  padding: 80px 50px 50px 50px;
  width: 500px;
  left: 50%;
  transform: translateX(-50%);
}
#login-container #form-container label {
  font-size: 18px;
}
#login-container #form-container button {
  font-size: 18px;
  width: 100px;
  height: 40px;
  border-radius: 15px;
  margin-top: 30px;
}
